<script type="es6">
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      lang: "",
      tabsNum: 1,
      numNft:1,
      showL: false,
       isApprove: false,
       coinBalance:0,
       nft:0,
       getNft:1,
       numAddr:'',
       link:''
    };
  },
  computed: {
    ...mapState("main", ["buyNftContract", "account","mxtCoinContract","getNftContract","getNftInfo"]),
  },
  methods: {
    ...mapActions("main", ["add_buyNftContract","add_mxtCoinContract","add_getNftContract"]),
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    showLang: function () {
      this.showL = !this.showL;
    },
        // 复制成功时的回调函数
        onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    changeLocale(locale) {
      localStorage.setItem("lang", locale);
      this.changeAppLocale(locale)
      this.showL = !this.showL;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    buyNFT:function(){
      var _this = this;
       var account = _this.account;
      if (this.buyNftContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      if (!this.numAddr) {
        this.$toast(this.$t("message.noAddr"));
        return;
      }
      console.log(this.numNft.toString(),this.numAddr.toString())
      this.$toast.loading({
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(this.numNft);
      this.buyNftContract.methods
        .mint(this.numNft.toString(),'0xA4B0cD431C278B547DD9D972bB4caDD59116D5f4')
        .send({ from: account })
        .then((res) => {
          console.log("buyNFT", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    getNFT:function(){
      var _this = this;
       var account = _this.account;
      if (this.getNftContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      if(_this.getNft>100){
        _this.getNft = '';
          this.$toast(this.$t("message.f_71"));
        return;
      }
      this.$toast.loading({
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.getNftContract.methods
        .give(_this.getNft.toString())
        .send({ from: account })
        .then((res) => {
          _this.add_getNftContract();
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    prev: function () {
      this.$refs.swiper.prev();
    },
    next: function () {
      this.$refs.swiper.next();
    },
      // USDT授权
    lpApprove: function () {
      if (this.mxtCoinContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = '0xee0c8e3df174e7871251e764ca26eb98033edc19';
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtCoinContract.methods
        .approve(address, "10000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
       // 是否授权
    isApproveFun: function () {
      var account = this.account;
      var address = '0xee0c8e3df174e7871251e764ca26eb98033edc19';

      this.mxtCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          console.log('usdt=========isApproveFun',res)
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isApprove = true;
          }
        });
    },
          //获取当前币种余额
  getInfo: function () {
      if (this.buyNftContract) {
        var account = this.account;
        this.buyNftContract.methods
          .getInfo(account)
          .call()
          .then((res) => {
            console.log(res)
            this.nft=res[3];
          });
      }
    },
       //获取当前币种余额
  getCoinBalance: function () {
      if (this.mxtCoinContract) {
        var account = this.account;
        this.mxtCoinContract.methods
          .balanceOf(account)
          .call()
          .then((res) => {
            var num = web3.utils.fromWei(res, "ether");
            num = num.toString();
            let index = num.indexOf(".");
            if (index !== -1) {
              num = num.substring(0, 6 + index + 1);
            } else {
              num = num.substring(0);
            }
            this.coinBalance = num;
          });
      }
    },
    getUrl: function (addr) {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      console.log(hash);
      var url = window.location.origin + window.location.pathname + "#/home/";
      var newAddr = "";
      if (addr) {
        newAddr = addr.replace('"', "").replace('"', "");
        this.link = url + "?invite=" + newAddr;
      }
      return this.link;
    },
    
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "en");
    var getData = () => {
      this.add_buyNftContract();
      this.add_mxtCoinContract();
      this.getCoinBalance();
      this.add_getNftContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!_this.buyNftContract || !_this.mxtCoinContract || !_this.getNftInfo) {

          getData();
        } else {
          this.isApproveFun();
          this.getInfo();
          console.log(window.location.host)
          this.link = window.location.host+'/#/?invite='+this.account;
          if(this.$route.query.invite){
            this.numAddr= this.$route.query.invite;
          }

          if(Number(this.getNftInfo[3])>100){
            _this.getNft = 100;
            
            }else{
              _this.getNft =_this.getNftInfo[3];
            }
          clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 60000);
  },
  // beforeCreate(){
  //   var h = window.location.hash;
  //   console.log(window)
  //   window.location.href='https://flarenft.github.io/'+h;
  // },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>

